.b-consult .block__cont {
  display: flex;
}

.b-consult .block__hd {
  margin-bottom: 20px;
}

.b-consult .block__image {
  position: absolute;
  width: 327px;
  left: -135px;
  bottom: -50px;
}

.b-consult .block__data {
  margin-left: auto;
  width: 151px;
}

.b-consult .block__form.wf {
  margin-top: 25px;
}

.b-consult .block__row.form-item {
  display: flex;
}

.b-consult .block__row.form-item input {
  margin-right: 8px;
}

.b-consult .block__col_form {
  flex: 1;
  min-width: 402px;
}

.b-consult .form-item textarea {
  min-height: 161px;
}

.b-consult .block__col_contact {
  margin-top: 25px;
  position: relative;
  flex: 1;
}

.b-consult .block__position {
  margin-bottom: 8px;
}

.b-consult .block__name {
  font-size: 20px;
  font-weight: 700;
  color: #333333;
  margin-bottom: -4px;
}

.b-consult .block__phone {
  margin-bottom: 9px;
}

.b-consult .block__phone a {
  font-weight: 700;
  font-size: 26px;
  border: none;
  color: #333333;
}

.b-consult .block__email {
  margin-bottom: 20px;
}

.b-consult .block__email a {
  font-size: 20px;
  border: none;
}

@media only screen and (min-width: 768px) {
  .b-consult .block__hd {
    margin-bottom: 30px;
  }

  .b-consult .block__image {
    width: 300px;
    bottom: -60px;
    left: -120px;
  }

  .b-consult .block__data {
    width: 204px;
    margin-right: 5px;
  }

  .b-consult .form-item textarea {
    min-height: 123px;
  }
}

@media only screen and (min-width: 1024px) {
  .b-consult .block__hd {
    margin-bottom: 40px;
  }

  .b-consult .block__image {
    width: 437px;
    bottom: -110px;
  }

  .b-consult .block__data {
    margin-right: 25px;
  }

  .b-consult .form-item textarea {
    min-height: 161px;
  }
}

@media only screen and (min-width: 1280px) {
  .b-consult .block__image {
    width: 492px;
    bottom: -110px;
  }
}

@media only screen and (max-width: 1023px) {
  .b-consult .block__row.form-item {
    max-width: 211px;
  }

  .b-consult .block__name {
    font-size: 15px;
    line-height: 28px;
  }

  .b-consult .block__phone a {
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 15px;
  }

  .b-consult .block__email {
    margin-bottom: 15px;
  }

  .b-consult .block__email a {
    font-size: 15px;
  }
}

@media only screen and (max-width: 767px) {
  .b-consult .block__col_form {
    display: none;
  }

  .b-consult .block__col_contact {
    max-width: 300px;
    margin: auto;
  }
}